import Parse from 'parse'

export function getAllCourses (phone) {
  return Parse.Cloud.run('getAllCourses', {}).then((res) => {
    let wrapRes = {
      mon: [],
      tue: [],
      wed: [],
      thu: []
    }
    for (let course of res) {
      course.teacherName = course.teacher.get('name')
      course.submitted = course.submitted_student + '/' + course.maximum_num
      if (course.days.indexOf('1') !== -1) {
        wrapRes.mon.push(course)
      }
      if (course.days.indexOf('2') !== -1) {
        wrapRes.tue.push(course)
      }
      if (course.days.indexOf('3') !== -1) {
        wrapRes.wed.push(course)
      }
      if (course.days.indexOf('4') !== -1) {
        wrapRes.thu.push(course)
      }
    }
    return Parse.Promise.as(wrapRes)
  })
}

export function downloadStudentCourse (obj) {
  return Parse.Cloud.run('downloadStudentCourse', obj)
}
export function downloadCourse (obj) {
  return Parse.Cloud.run('downloadCourse', obj)
}
export function update(id, obj) {
  return Parse.Cloud.run('updateASA', {id: id, obj: obj})
}



